const joinForm = document.querySelector(".join-form");

if (joinForm) {
  const STEPS_NUMBER = 3;
  const nextButton = joinForm.querySelector(".join-form__next-button");
  const formFields = joinForm.querySelectorAll(".join-form__input-wrapper");

  let currentStep = 0;

  nextButton.addEventListener("click", () => {
    currentStep++;
    if (currentStep < STEPS_NUMBER) {
      formFields.forEach((field) => field.classList.add("join-form__input-wrapper--hidden"));
      document
        .querySelector(`#step-${currentStep}`)
        .classList.remove("join-form__input-wrapper--hidden");
    } else {
    }
  });
}
